@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&family=Fira+Mono:wght@500&display=swap);
@media screen and (min-width: 992px){.new-course-video{margin-left:90px}}
.navbar{height:100px !important}.navbar-bottom-container{display:none !important;height:0px !important}.footer{display:none !important}.not-found{height:calc(100vh - 100px);padding-top:5vh;background:#000;text-align:center;color:#e0e0e0;font-family:"Fira Mono",monospace}.not-found svg{width:clamp(300px,70%,600px);height:50vh}.not-found #rocket{-webkit-transform:translateY(750px);transform:translateY(750px);-webkit-animation:launch 2s ease-out forwards;animation:launch 2s ease-out forwards}@-webkit-keyframes launch{from{-webkit-transform:translateY(750px);transform:translateY(750px)}to{-webkit-perspective:500px;perspective:500px;-webkit-transform:translateY(0px);transform:translateY(0px)}}@keyframes launch{from{-webkit-transform:translateY(750px);transform:translateY(750px)}to{-webkit-perspective:500px;perspective:500px;-webkit-transform:translateY(0px);transform:translateY(0px)}}.not-found #stars{-webkit-animation:twinkling 2s linear;animation:twinkling 2s linear}@-webkit-keyframes twinkling{from{-webkit-transform:scale(0);transform:scale(0)}to{-webkit-transform:scale(1);transform:scale(1)}}@keyframes twinkling{from{-webkit-transform:scale(0);transform:scale(0)}to{-webkit-transform:scale(1);transform:scale(1)}}.not-found .text{opacity:0;-webkit-animation:appear 1s ease-in forwards;animation:appear 1s ease-in forwards;-webkit-animation-delay:1.8s;animation-delay:1.8s;background-color:#000;padding:0px 30px 0px 30px}.not-found .text h1{font-size:3rem;font-family:"Permanent Marker",cursive}.not-found .text div{-webkit-transform-style:preserve-3d;transform-style:preserve-3d}.not-found .text h2{font-size:1.5rem}.not-found .text h3{font-size:1.2rem}.not-found .text h3 a{color:#f66947 !important;text-decoration:none;font-weight:bold !important}@-webkit-keyframes appear{from{opacity:0}to{opacity:1}}@keyframes appear{from{opacity:0}to{opacity:1}}
